import { graphql } from 'gatsby';
import { Layout } from '../components/Layout/Layout';
import React, { useState } from 'react';
import { ComboboxData, Stack, Table, Title } from '@mantine/core';
import { SortableTableHeader } from '../components/SortableTableHeader';
import { DangerRatingFilters } from '../components/DangerRatingFilters';
import { BarChart } from '@mantine/charts';
import { DangerRatingChart } from '../components/DangerRatingChart';

export type DriverDataItem = {
  id: string;
  Driver: string;
  ControlLosses: number;
  Active: boolean;
  Races: number;
  Offtracks: number;
  Laps: number;
  Inc: number;
  CarContacts: number;
  BlackFlags: number;
  Contacts: number;
  SR: number;
  DR: number;
};

type PageData = {
  allWcrDr2Csv: {
    nodes: DriverDataItem[];
  };
};

export type DangerRatingFilterState = {
  gt100: boolean;
  active: boolean;
  driverNames: string[];
  sortBy: keyof DriverDataItem;
  sortDir: 'asc' | 'desc';
};

export type DangerRatingFilterStateSetter = (partialFilter: Partial<DangerRatingFilterState>) => void;
export type DangerRatingFilterSortHandler = (field: keyof DriverDataItem) => void;

const calculateDr = ({ ControlLosses, CarContacts, Contacts, Laps }: DriverDataItem) => {
  const rawValue = (ControlLosses * 2 + (CarContacts + Contacts) * 4) / Laps;

  if (isNaN(rawValue)) return 0;

  return Number(rawValue.toFixed(3));
};
const calculateSr = ({ Offtracks, ControlLosses, CarContacts, Contacts, Laps }: DriverDataItem) => {
  const rawValue = (Offtracks + ControlLosses * 2 + (CarContacts + Contacts) * 4) / Laps;

  if (isNaN(rawValue)) return 0;

  return Number(rawValue.toFixed(3));
};

const DangerRating: React.FC<{
  data: PageData;
}> = ({ data }) => {
  const driverData = data.allWcrDr2Csv.nodes.map((row) => ({
    ...row,
    SR: calculateSr(row),
    DR: calculateDr(row),
  }));

  const [filters, _setFilters] = useState<DangerRatingFilterState>({
    gt100: false,
    active: false,
    driverNames: [],
    sortBy: 'Driver',
    sortDir: 'asc',
  });

  const handleSetFilters = (partialFilter: Partial<typeof filters>) => {
    _setFilters({
      ...filters,
      ...partialFilter,
    });
  };

  const handleSortClick = (field: keyof DriverDataItem) => {
    if (field === filters.sortBy) {
      handleSetFilters({
        sortDir: filters.sortDir === 'asc' ? 'desc' : 'asc',
      });
    } else {
      handleSetFilters({
        sortBy: field,
        sortDir: 'asc',
      });
    }
  };

  const sortAsc = (a: DriverDataItem, b: DriverDataItem) => {
    if (a[filters.sortBy] > b[filters.sortBy]) {
      return 1;
    } else if (a[filters.sortBy] < b[filters.sortBy]) {
      return -1;
    }

    return 0;
  };

  const sortDesc = (a: DriverDataItem, b: DriverDataItem) => {
    if (b[filters.sortBy] > a[filters.sortBy]) {
      return 1;
    } else if (b[filters.sortBy] < a[filters.sortBy]) {
      return -1;
    }

    return 0;
  };

  const driverNameFilterOpts: ComboboxData = driverData.map((driver) => ({
    label: driver.Driver,
    value: driver.Driver,
  }));

  const filteredDriverData = driverData
    .filter((row) => (filters.active ? row.Active : true))
    .filter((row) => (filters.gt100 ? row.Laps > 100 : true))
    .filter((row) => (filters.driverNames.length > 0 ? filters.driverNames.includes(row.Driver) : true))
    .sort(filters.sortDir === 'asc' ? sortAsc : sortDesc);

  const rows = filteredDriverData.map((row) => (
    <Table.Tr key={row.id}>
      <Table.Td>{row.Driver}</Table.Td>
      <Table.Td>{row.Active ? 'Active' : 'Inactive'}</Table.Td>
      <Table.Td>{calculateDr(row).toFixed(3)}</Table.Td>
      <Table.Td>{calculateSr(row).toFixed(3)}</Table.Td>
      <Table.Td>{row.Races}</Table.Td>
      <Table.Td>{row.Laps}</Table.Td>
      <Table.Td>{row.Inc}</Table.Td>
      <Table.Td>{row.Offtracks}</Table.Td>
      <Table.Td>{row.ControlLosses}</Table.Td>
      <Table.Td>{row.CarContacts}</Table.Td>
      <Table.Td>{row.Contacts}</Table.Td>
      <Table.Td>{row.BlackFlags}</Table.Td>
    </Table.Tr>
  ));

  return (
    <Layout>
      <Stack>
        <Title order={1}>Danger Ratings</Title>
        <DangerRatingChart driverData={filteredDriverData} />
        <DangerRatingFilters filters={filters} setFilters={handleSetFilters} driverNames={driverNameFilterOpts} />
        {/* <div>Result Count: {rows.length}</div> */}
        <Table>
          <Table.Thead>
            <Table.Tr>
              <SortableTableHeader
                active={filters.sortBy === 'Driver'}
                variant='string'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Driver')}
              >
                Driver Name
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'Active'}
                variant='string'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Active')}
              >
                Status
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'DR'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('DR')}
              >
                DR
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'SR'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('SR')}
              >
                SR
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'Races'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Races')}
              >
                Races
              </SortableTableHeader>

              <SortableTableHeader
                active={filters.sortBy === 'Laps'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Laps')}
              >
                Laps
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'Inc'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Inc')}
              >
                Incidents
              </SortableTableHeader>

              <SortableTableHeader
                active={filters.sortBy === 'Offtracks'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Offtracks')}
              >
                Off Tracks
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'ControlLosses'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('ControlLosses')}
              >
                Control Losses
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'CarContacts'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('CarContacts')}
              >
                Car Contacts
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'Contacts'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('Contacts')}
              >
                Contacts
              </SortableTableHeader>
              <SortableTableHeader
                active={filters.sortBy === 'BlackFlags'}
                variant='number'
                sortDir={filters.sortDir}
                onClick={() => handleSortClick('BlackFlags')}
              >
                Black Flags
              </SortableTableHeader>
            </Table.Tr>
          </Table.Thead>
          <Table.Tbody>{rows}</Table.Tbody>
        </Table>
      </Stack>
    </Layout>
  );
};

export const query = graphql`
  query {
    allWcrDr2Csv(sort: { Driver: ASC }) {
      nodes {
        Driver
        ControlLosses
        Active
        id
        Races
        Offtracks
        Laps
        Inc
        CarContacts
        BlackFlags
        Contacts
      }
    }
  }
`;

export default DangerRating;
